.icon {
  display: block;
  width: 18px;
  padding-bottom: 100%;
  
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  
  @include onDesktop {
    width: 24px;
    height: 24px;
  }
  
  @include ontablet() {
    width: 24px;
    height: 24px;
  }
  
  
  
  &--menu {
    background-image: url(../images/menu.svg);
  
    &:hover {
      background-image: url(../images/menu_hover.svg);
    }
  }
  
  &--phone {
    background-image: url(../images/phone.svg);
  }
  
  &--cross {
    background-image: url(../images/images.bike/images_icon/darkVector.svg);
  }
}
